.popup-containerglan {
  position: fixed;
  top: 49%;
  left: 50%;
  /* height: 95%; */
  transform: translate(-50%, -50%);
  background-color: #fff;
  border-radius: 10px;
  padding: 20px;
  box-shadow: 1px 3px 5px 2px #e3d7d7;
  z-index: 1000;
  width: 500px;
  }
  
  .popup-headerlan {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-top: -3%;
  }

  .popup-headerlan img {
    max-width: 8%;
  }
  
  .popup-titlelan {
    font-size: 18px;
  }
  
  .close-button {
    border: none;
    background: none;
    cursor: pointer;
    font-size: 20px;
    color: #333;
    float: right;
    
  }
  
  .close-button:hover{
    background-color: red;
   
  }


  .popup-contentlan {
    margin-top: -2%;
  }
  
  .form-group {
    margin-bottom: 10px;
  }
  
  .test-connection-button {
    padding: 8px 16px;
    background-color: #007bff;
    color: #fff;
    border: none;
    border-radius: 5px;
    cursor: pointer;
  }

  .lanform-btns {
    display: flex;
    justify-content: space-around;
  }
  
  .connection-result {
    margin-top: 10px;
  }
  

  .form-group p{
    cursor: pointer;
    color: #007bff;
  }

  .alert-status {
    background-color: #fff;
    width: auto;
    padding: 10px;
    border-radius: 10px;
  }

  .alert-status p {
    text-align: center;
    color: #007bff;
  }

  .alert-status button {
    display: flex;
    margin-left: 4rem;
    border: 1px solid #ccc;
    border-radius: inherit;
  }

  .alert-status button:hover {
    color: #ffff;
    background-color: #007bff;
  }

  .file-browserlan {
    display: flex;
    flex-wrap: wrap;
    width: auto;
    padding: 20px;
    gap: 1rem;
  }

  .popup-Lanbrowse {
  width: 138%;
  height: 85%;
  position: absolute;
  background-color: #fff;
  padding: 20px;
  border-radius: 5px;
  margin-top: 4rem;
  margin-left: 6rem;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.2);
  overflow-y: scroll;
  }
  
  .popup-Lanbrowse h2 {
    color: #000;
    font-weight: bolder;
  }


  .lanpopup-close-btn {
    position: absolute;
    margin-left: 23rem;
    margin-top: -15rem;
    color: #000;
    font-size: 30px;
    z-index: 1;
  }

  .lanfolder-content {
    display: grid;
    justify-items: center;
    cursor: pointer;
    text-align: center;
    padding: 5px;
    width: 149px;
  }

  .lanfolder-content img {
    max-width: 35%;
    /* cursor: pointer; */
  } 

  /* .lanfolder-content span {
    text-align: center;
  } */


  
  .selected-folder {
    border-radius: 10px;
    background-color: lightblue;
    box-shadow: 1px  3px 5px #808080;

  }
 
  .lancomputer-content {
    display: grid;
    justify-items: center;
    cursor: pointer;
    text-align: center;
    padding: 5px;
    width: 163px;
  }


  .lancomputer-content:hover{
    cursor: pointer;
background-color: #fcf7b6;
  }

  .lancomputer-content img {
    max-width: 55%;
    /* cursor: pointer; */
  } 

  .selected-computer {
    border-radius: 10px;
    background-color: #eaf248;
    height: fit-content;
    box-shadow: 1px  3px 5px #808080;

  }

  .popup-actions button {
    float: right;
    margin-top: -2.5rem;
    font-size: x-large;
  }
  

  .lanservername-icon {
    position: absolute;
    margin-left: 86%;
    margin-top: 7%;
    cursor: pointer;
    font-size: 20px;

  }




