.setting-container {
  top: 0;
  left: 0;
  justify-content: center;
  align-items: center;
}

.setting-title h3 {
  display: flex;
  align-items: center;
  position: absolute;
  margin-top: 4rem;
  gap: 10px;
  margin-left: 10%;
  color: rgba(14, 85, 185, 0.9019607843);
}

.setting-menutab-content {
  position: fixed;
  width: 23%;
  display: grid;
  justify-content: justify;
  background-color: #ffff;
  border: 1px solid #ccc;
  margin-top: 7rem;
  margin-left: 7%;
  padding: 10px;
  border-radius: 5px;
  cursor: pointer;
}

.setting-menutab-content span link {
  margin: 8px;
}

.admin-settingprofile {
  display: flex;
  justify-content: space-around;
  align-items: center;
  padding: 7px;
  border-radius: 5px;
  margin-bottom: 10px;
  background: linear-gradient(45deg, black, transparent);
}

.setting-container span {
  display: grid;
  text-align: justify;
  /* gap: 5px; */
  color: #000;
  margin-left: 1rem;
}


.admin-settingprofile img {
  max-width: 35%;
  border-radius: 50px;
}


.Setting-breadcrumb h4 {
  position: absolute;
  margin-left: 31rem;
  margin-top: 4.5rem;
}

.setting-menutab-ul ul li {
  text-align: justify;
  margin: 5px;
}

.setting-menutab-ul ul li a {
  color: #000000df;
}



.setting-menutab-ul ul {
  height: 300px;
  list-style-type: none;
  padding: 0;
  overflow-y: scroll;
}

.setting-menutab-ul li {
  padding: 5px;
  position: relative;
}

.setting-menutab-ul li.active a {
  background-color: #007bff;
  color: #fff;
  padding: 5px;
  border-radius: 5px;
  box-shadow: 1px 1px 1px #808080;
  font-weight: bold;
}

