.setting-menutab-ul li a {
  text-decoration: none;
  color: #333; /* Default text color */
  display: flex;
  align-items: center;
}

.setting-menutab-ul li i {
  margin-right: 8px;
}


.submenu {
  margin-top: 5px;
  padding-left: 20px;
}

.submenu a.active {
  background-color: #007bff;
  color: #fff;
  padding: 5px;
  border-radius: 5px;
  box-shadow: 0px 2px 3px #808080;
  font-weight: bold;
}

.submenu a {
  display: block;
  text-decoration: none;
  color: #333; /* Default text color for submenu items */
  padding: 5px 0;
}
