.sidebar {
  width: 80px;
  height: 100vh;
  background: #fff;
  position: fixed;
  transition: width 0.01s ease;
  overflow: hidden;
  margin-top: 4rem;
  z-index: 5;
  box-shadow: 1px 1px 12px #888888;
}

.sidebar.open {
  width: 150px;
  box-shadow: 1px 1px 5px #888888;
}

.sidebar-content {
  display: flex;
  flex-direction: column;
  height: 100%;
  margin-top: 3rem;
}

@media screen and (max-width:768px) {
  .sidebar {
    width: 80px;
    height: 100%;
    background: #fff;
    position: fixed;
    transition: width 0.01s ease;
    overflow: hidden;
    margin-top: 4rem;
    z-index: 5;
    box-shadow: 1px 1px 12px #888888;
}
}


@media screen and (max-width:540px) {
