  }

}

.nav-links {
  padding: 0;
  margin: 0;
  list-style: none;
}

.nav-links li {
  position: relative;
  width: 100%;
}

.nav-links a,
.nav-links .icon-link {
  display: flex;
  align-items: center;
  height: 46px;
  text-decoration: none;
  color: black;
  padding: 0 30px;
}

.nav-links .icon-link {
  cursor: pointer;
}

.nav-links i {
  font-size: 20px;
  height: 30px;
  width: 25px;
  display: flex;
  text-align: center;
  align-items: center;
  justify-content: center;
  transition: all 0.1s ease;
}
.nav-links i:hover {
  font-size: 30px;
}

.menuss-link-name {
  display: none;
  margin-left: 10px;
}

.sidebar.open .menuss-link-name {
  display: inline;
  transition: all 0.2s;
}

.menuss-link-name:hover {
  font-size: larger;
}

.home-section {
  position: absolute;
  margin-left: 80px;
  transition: margin-left 0.3s ease;
}

.sidebar.open ~ .home-section {
  margin-left: 250px;
}

.sidebar.open .bx-chevron-right {
  visibility: hidden;
  transition: all 0.01s;
}
.sidebar.close .bxs-pin {
  visibility: hidden;
  transition: all 0.01s;
}

.home-content {
  display: flex;
  align-items: center;
  padding: 20px;
}

.bx-chevron-right {
  position: fixed;
  margin-left: -2rem;
  margin-top: 2rem;
  z-index: 1;
  font-size: 2rem;
  border-radius: 50%;
  background-color: #007bff;
  color: #fff;
  visibility: visible;
  transition: all 0.01s;
  cursor: pointer;
}
.bxs-pin {
  position: fixed;
  margin-left: 2rem;
  margin-top: 2rem;
  z-index: 1;
  font-size: 2rem;
  border-radius: 50%;
  background-color: #007bff;
  color: #fff;
  visibility: visible;
  transition: all 0.01s;
  cursor: pointer;
}

.bx-chevron-right:hover + .text,
.bxs-pin:hover + .text {
  display: inline;
}

.text {
  display: none;
  color: black;
  margin-left: 10px;
}

/* Here the End Side Bar Container Css */

.sidebar-close-img {
  position: absolute;
  max-width: 50%;
  margin-top: 8%;
  margin-left: 1.5rem;
}
.sidebar-open-img {
  position: absolute;
  max-width: 58%;
  margin-top: 2%;
  margin-left: 2rem;
}


.sidebar.open .sidebar-close-img {
  display: none;
}

/*This is the start of the Hover Icon Name show in side bar*/
.sidebar.open .left {
  visibility: hidden;
  opacity: 0;
}

.sidebar.close .left {
  visibility: hidden;
}
.sidebar.close a:hover .left {
  margin-left: 2rem;
  width: 0;
  height: 0;
  border-top: 10px solid transparent;
  border-right: 13px solid #000;
  border-bottom: 10px solid transparent;
  position: fixed;
  visibility: visible;
}

.sidebar.open .HoverName {
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
}

.sidebar.close .HoverName {
  visibility: hidden;
}

.sidebar.close a:hover .HoverName {
  position: fixed;
  margin-left: 2.5rem;
  padding-top: 0px;
  border-radius: 5px;
  background-color: #000;
  color: #fff;
  height: 2rem;
  min-width: 4.5rem;
  display: flex;
  text-align: center;
  align-items: center;
  justify-content: center;
  visibility: visible;
}
/*This is the End of the Hover Icon Name show in side bar*/

