/* ProfileMenu.css */

.profile-menu {
  position: relative;
  display: inline-block;
}

.profile {
  margin-left: 4%;
  cursor: pointer;
  margin-top: 3%;
}

.dropdown-menu {
  width: 120%;
  display: flex;
  flex-direction: column;
  position: absolute;
  margin-left: -8rem;
  padding: 10px;
  top: 56px;
  /* Adjust based on profile picture size */
  background-color: #fff;
  border: 1px solid #ddd;
  border-radius: 4px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.2);
  z-index: 1000;
  transition: 1s;
}

.menu-item {
  /* padding: 10px 20px; */
  border-bottom: 1px solid #ddd;
  text-align: justify;
  cursor: pointer;
  margin-left: -0.5rem;
  background: none;
  border: none;
  width: 100%;
}

.menu-item:last-child {
  border-bottom: none;
}

.menu-item:hover {
  background-color: #f5f5f5;
}


.dropdown-adminprofile {
  display: flex;
  align-items: baseline;
  margin-bottom: 5px;
}

.dropdown-adminprofile span {
  display: grid;
  width: 300%;
}

.dropdown-adminprofile div {
  position: absolute;
  color: #ffff;
  font-size: 25px;
  margin-top: -2rem;
  margin-left: 8rem;
}

.agent-count {
  background-color: red; /* Example background color */
  color: white; /* Text color */
  border-radius: 50%; /* Make it circular */
  padding: 0.5em 0.75em; /* Adjust padding as needed */
  font-size: 0.75em; /* Adjust font size */
  position: absolute; /* Position it as needed */
  top: 0; /* Adjust position */
  right: 0; /* Adjust position */
}


