.customer-care-container {
    position: fixed;
    bottom: 40px;
    right: 20px;
    display: flex;
    align-items: center;
    cursor: pointer;
    z-index: 2;
  }
  
  .icon-container {
    position: absolute;
    background-color: #ffff;
    border-radius: 50%;
    padding: 10px;
    display: flex;
    align-items: center;
    margin-left: 6rem;
    justify-content: center;
    transition: transform 0.3s ease;
    box-shadow: 1px 1px 1px 1px #808080;
  }
  
  .icon-container .support-img {
    width: 24px;
    height: 24px;
    max-width: fit-content;
  }
  
  
  @keyframes zoom {
  
    0%,
    100% {
      transform: scale(1);
    }
  
    30% {
      transform: scale(1.1);
    }
  }
  
  .icon-container {
    animation: zoom 2s infinite;
  }
  
  .tag {
    background-color: #ffff;
    color: #808080;
    padding: 5px 10px;
    border-radius: 5px;
    font-size: 14px;
    margin-left: 10px;
    opacity: 0;
    transition: opacity 0.3s ease, transform 0.3s ease;
    transform: translateX(100%);
    /* box-shadow: 1px 1px 1px 1px #808080; */
  }
  
