.password-container {
  padding: 20px;
  /* background-color: #f5f5f5; */
  border-radius: 10px;
  /* box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1); */
}

.password-container.pinned {
  position: fixed;
  top: 0;
  right: 0;
  width: 300px;
}

.password-container {
  position: relative;
}

.password-content {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  max-width: 800px;
  height: 77%;
  margin-top: 8rem;
  margin-left: 26rem;
  background-color: #fff;
  z-index: 10;
  border-radius: 5px;
  padding: 20px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  overflow-y: scroll;
}

.passwordch-container {
  display: flex;
  justify-content: center;
  align-items: center;
  /* height: 100vh;
  background-color: #f4f4f4; */
}

.passwordch-content {
  background: #fff;
  border-radius: 8px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  padding: 20px;
  width: 100%;
  max-width: 400px;
}

.passwordch-content img {
  display: flex;
  margin-left: 10rem;
}

.passwordch-step {
  display: flex;
  flex-direction: column;
}

.passwordch-step h2 {
  margin-bottom: 20px;
  font-size: 1.5em;
  color: #333;
}

.passwordch-step input {
  margin-bottom: 15px;
  padding: 10px;
  font-size: 1em;
  border: 1px solid #ddd;
  border-radius: 4px;
}

.chnagepassword-submitbtn button {
  padding: 10px;
  font-size: 1em;
  color: #fff;
  background-color: #007bff;
  border: none;
  border-radius: 4px;
  cursor: pointer;
}

.chnagepassword-submitbtn button:hover {
  background-color: #0056b3;
}

.password-input-group {
  position: relative;
  margin-bottom: 1rem;
}

.password-toggle-btn {
  position: absolute;
  right: 10px;
  top: 40%;
  transform: translateY(-50%);
  border: none;
  background: transparent;
  cursor: pointer;
}
