.successJobs-container {
  /* display: grid; */
  margin-top: 12.5rem;
  margin-left: 7rem;
}

.timeselect{
  position: absolute;
  width: 48%;
  padding: 8px;
  border-radius: 4px;
  box-sizing: border-box;
  margin-top: 2%;
  margin-left: 0%;
}

.canvas{
  
  height: 300px !important;
}

.nodes-cards {
  display: none;
}

.nodes-cards.selected {
  display: block;
  background-color: #ffff;
}

.nodes-cards button {
  height: 5%;
  color: #000;
  border: 1px solid #000;
  background: none;
  margin-left: 1rem;
  margin-top: 5rem;
}

.nodes-cards button:hover {
  color: #ffff;
  background-color: #277DB0;
}

.nodes-cards {
  width: auto;
  height: auto;
  border: 1px solid #ccc;
  padding: 10px;
  margin: 1rem 0rem 0rem 0rem;
  border-radius: 3px;
  background-color: #ffff;
  cursor: pointer;
  font-size: 25px;
  box-shadow: 1px 5px 8px #ccc;
}


.successJobs-container select {
  position: absolute;
  width: 20%;
  padding: 8px;
  border: 1px solid #ccc;
  border-radius: 4px;
  box-sizing: border-box;
  margin-top: -6.8%;
  margin-left: 49rem;
}


.loading-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.loading-indicator {
  max-width: 50%;
}


.node-cards-container {
  display: flex;
  justify-content: space-around;
  align-items: flex-start;
  flex-wrap: wrap;
  top: 0;
  margin-left: -1rem;
}

.node-cards {
  width: auto;
  height: auto;
  border: 1px solid #ccc;
  padding: 10px;
  margin: 1rem 0rem 0rem 0rem;
  border-radius: 3px;
  background-color: #ffff;
  cursor: pointer;
  font-size: 25px;
  box-shadow: 1px 5px 8px #ccc;
}


.node-cards span {
  color: green;
  float: right;
  font-size: 25px;
}

.node-cards h3 {
  justify-content: right;
  font-size: 25px;
}

.count-viewS {
  display: flex;
  justify-content: center;
}

.count-viewS p {
  justify-content: center;
  margin-top: 6rem;
  margin-left: 4rem;
}

.count-viewS-bar p {
  text-align: center;
}



.node-cards button {
  height: 5%;
  color: #000;
  border: 1px solid #000;
  background: none;
  margin-left: 1rem;
}

.node-cards button:hover {
  color: #ffff;
  background-color: #277DB0;
}


.popups-jobs .success-close-btn {
  position: absolute;
  margin-left: 70%;
  color: #ffff;
  font-size: 30px;
}



.bar-chart-container {
  width: auto;
}


.popup-jobs .close-btn {
  position: absolute;
  margin: -2rem 0 0 59rem;
  font-size: 30px;
  color: #000;
  cursor: pointer;
} 





.pie-chart {
  box-sizing: border-box;
  display: block;
  height: 200px;
  width: 200px;
  margin: 0 0 0 1rem;
}

.menu-button {
  color: #277DB0;
  background-color: #ffff;
  padding: 10px 15px;
  margin: 5px;
  border: 1px solid #277DB0;
  border-radius: 5px;
  cursor: pointer;
  text-decoration: none;
  transition: background-color 0.3s, color 0.3s;
  width: 100%;
}

.menu-button:hover {
  background-color: #277DB0;
  color: #ffff;
}

.menu-button.active {
  background-color: #277DB0;
  color: #ffff;
}



.jobsuss-container{
  height: fit-content;
  width: auto;
  display: flex;
  flex-direction: column;
  margin: 10px;
  margin-top: 2rem;
}
.jobsuss-container .jobsuss-container-btn{
  height: 20%;
  color: #000;
  border: 1px solid;
  background: none;
  margin: 5px;
}

.card-view-more-btn button {
  color: #000000d3;
  margin-left: 45%;
  background: none;
  margin-top: 1rem;
  padding: 10px;
  border: none;
  cursor: pointer;
  border-radius: 5px;
  border: 1px solid #ccc;
}

.card-view-more-btn button:hover {
  color: #ffff;
  background-color: #277DB0;
}


.SearchJ input[type="text"] {
  position: absolute;
  margin-right: 10px;
  margin-top: -5.5rem;
  width: 20%;
  border: 1px solid #000;
  margin-left: 2%;
}





.dwnldbtn {
  width: 20%;
  padding: 10px 20px;
  border: 1px solid #000000d3;
  border-radius: 5px;
  background-color: white;
  color: #000000d3;
  cursor: pointer;
  font-size: 16px;
  transition: background-color 0.3s, color 0.3s;
}


.dwnldbtn :hover {
  color: #ffff;
  background-color: #000000d3;
}

.jobsuccess{
  justify-content: center;
  background: #000;
}



/* css for donwloadbuttons */
.download-options-container {
  position: relative;
}

.download-icon-container {
  display: flex;
  align-items: center;
  justify-content: center;
  float: right;
  margin-top: -5rem;
  margin-right: 3rem;
}





.download-options {
  position: absolute;
  top: 0;
  left: 63rem;
  width: max-content;
  margin-top: -3rem;
  transform: translateX(-50%);
  background-color: antiquewhite;
  box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.1);
  border: 2px solid #ffff;
  border-radius: 4px;
  padding: 8px;
  z-index: 1;
}

.download-option {
  cursor: pointer;
  display: flex;
  align-items: center;
  padding: 8px;
}

.option-icon {
  font-size: 24px;
  margin-right: 8px;
}


