.notificationAlert-containerr {
    width: 100%;
    /* border: 1px solid #ccc; */
    border-radius: 15px;
    overflow: hidden;
    transition: height 0.3s ease, transform 0.3s ease;
    position: fixed;
    bottom: 10px;
    right: 10px;
    /* height: 25%; */
    margin-right: 10rem;
    /* background-color: #fff; */
    z-index: 1;
}


@keyframes zoom {

    0%,
    100% {
      transform: scale(1);
    }
  
    20% {
      transform: scale(1.1);
    }
  }
  
  .notificationAlert-headerr {
    animation: zoom 2s infinite;
  }

.notificationAlert-container {
    width: 300px;
    /* border: 1px solid #ccc; */
    border-radius: 15px;
    overflow: hidden;
    transition: height 0.3s ease, transform 0.3s ease;
    position: fixed;
    bottom: 0px;
    right: 10px;
    margin-right: 10rem;
    background-color: #fff;
    z-index: 5;
    box-shadow: 1px 3px 5px #808080;
}

.notificationAlert-headerr {
   position: absolute;
   cursor: pointer;
   display: flex;
   align-items: center;
   justify-content: center;
   color: #fff;
   font-size: 24px;
   border-radius: 50%;
   padding: 10px;
   margin-top: 13px;
   margin-left: 94%;
   background-color: #007bff;
   transition: transform 0.3s ease;
   box-shadow: 1px 1px 1px 1px #808080;

   .notification-count {
       position: absolute;
       top: -8px; /* Adjust as needed */
       right: -4px; /* Adjust as needed */
       background-color: #f22;
       color: #fff;
       border-radius: 50%;
       padding: 5px;
       font-size: 14px;
       width: auto;
       height: 22px;
       display: flex;
       justify-content: center;
       align-items: center;
       box-shadow: 0px 1px 3px rgba(0, 0, 0, 0.25);
   }

   i.bxs-bell {
       margin-left: 5px; /* Adjust margin between count and bell icon */
   }
}

.notificationAlert-headerr span {
    width: auto;
    height: auto;
    align-items: center;
    display: flex;
    max-width: -moz-fit-content;
    max-width: fit-content;
}

.notificationAlert-input {
    display: flex;
}

.notificationAlert-input input {
    padding: 5px;
    margin-right: 5px;
}

.notificationAlert-input button {
    padding: 5px 10px;
}

.notificationAlert-content.expanded {
    max-height: 300px;
}

.notificationAlert-card {
    padding: 10px;
    border-bottom: 1px solid #eee;
    background-color: #fff;
}

.notificationAlert-footer {
    padding: 10px;
    text-align: center;
    cursor: pointer;
    background-color: #f4f4f4;
}

.notificationAlert-footer i {
    font-size: 20px;
}

.expanded .notificationAlert-content {
    max-height: 300px;
}

/* Popup animation */
.notificationAlert-container {
    animation: slideIn 0.5s forwards;
}

@keyframes slideIn {
    from {
        transform: translateY(100%);
    }
    to {
        transform: translateY(0);
    }
}


.notificationAlert-card {
    padding: 10px;
    border-bottom: 1px solid #ccc;
}

.drag-handle {
    width: 100%;
    height: 10px;
    cursor: ns-resize;
    background-color: #eee;
}

.enable-audio-button {
    background: none;
}

.notificationAlert-content-msg{
    max-height: 150px;
    overflow-y: auto;
    transition: max-height 0.3s ease;
}



.notificationAlert-headerr.vibrate i {
    animation: vibrate 0.3s linear infinite;
}

@keyframes vibrate {
    0% { transform: translateX(0); }
    25% { transform: translateX(-2px); }
    50% { transform: translateX(2px); }
    75% { transform: translateX(-2px); }
    100% { transform: translateX(0); }
}