/* Table grid styling */
/* Existing CSS */

/* Style for resizing handle */
.backup-grid th::after {
  content: '';
  position: absolute;
  top: 0;
  right: -4px; /* Adjust position as needed */
  bottom: 0;
  width: 8px; /* Width of the resizing handle */
  cursor: col-resize; /* Cursor style */
  z-index: 1; /* Ensure handle is above content */
  background-color: transparent;
  pointer-events: auto; /* Ensure handle receives mouse events */
}

/* Additional CSS for resize icons */
.backup-grid th {
  position: relative;
}

.backup-grid th .resize-icon {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  width: 20px; /* Width of the resize icon area */
  height: 20px; /* Height of the resize icon area */
  cursor: col-resize; /* Cursor style */
  z-index: 2; /* Ensure icons are above content */
  pointer-events: auto; /* Ensure icons receive mouse events */
}

.backup-grid th .resize-icon-left {
  left: -20px; /* Position to the left of the column */
}

.backup-grid th .resize-icon-right {
  right: -20px; /* Position to the right of the column */
}

/* Style for when hovering over the resize icons */
.backup-grid th .resize-icon:hover {
  background-color: #ddd;
}

/* Example styles for the resize icons */
.backup-grid th .resize-icon::before {
  content: '';
  position: absolute;
  top: 50%;
  width: 0;
  height: 0;
  border-style: solid;
  border-width: 5px 5px 5px 0; /* Adjust arrow size and direction */
  border-color: transparent #555 transparent transparent; /* Adjust arrow color */
}

.backup-grid th .resize-icon-left::before {
  left: 50%; /* Adjust left arrow position */
  transform: translate(-50%, -50%) rotate(135deg); /* Rotate the arrow */
}

.backup-grid th .resize-icon-right::before {
  right: 50%; /* Adjust right arrow position */
  transform: translate(50%, -50%) rotate(-45deg); /* Rotate the arrow */
}



/* Adjust the position and appearance of resize icons */
/* Adjust the position and appearance of resize icons */
.resize-icon {
  position: absolute;
  top: 72%;
  transform: translateY(-50%);
  width: 20px; /* Adjust width as needed */
  height: 50%; /* Match the height of the header cell */
  cursor: col-resize;
  z-index: 2;
  pointer-events: auto;
  text-align: center;
  margin-left: -2%;
}

.resize-icon-left::before,
.resize-icon-right::before {
  content: '\2194'; /* Unicode character for horizontal double arrow */
  font-size: 14px; /* Adjust font size as needed */
  line-height: 20px; /* Adjust line height to center vertically */
  /* color: #fa0000; Adjust color as needed */
  display: none;
}

.resize-icon-left::before {
  margin-left: -5px; /* Adjust to position the icon to the left */
}

.resize-icon-right::before {
