.progress-container {
  position: relative;
  width: 100%;
  height: 20px; /* Adjust height as needed */
  background-color: #e0e0e0; /* Light gray background */
  border-radius: 5px; /* Rounded corners */
  overflow: hidden;
}

.progress-bar {
  height: 100%;
  color: white; /* Text color inside the bar */
  text-align: center; /* Center text */
  line-height: 20px; /* Center text vertically */
  border-radius: 5px; /* Rounded corners */
  transition: width 0.4s ease; /* Smooth transition effect */
}

/* Ensure the table cell does not affect the progress bar */
.td3 {
  padding: 0; /* Remove padding if needed */
  overflow: hidden; /* Hide overflow to prevent elements from spilling out */
}

/* Container for the progress bar */
.progress-container {
  background-color: #ede2e2; /* White background for the unfilled part */
  border-radius: 4px;       /* Rounded corners */
  height: 20px;             /* Height of the progress bar */
  width: 100%;               /* Full width of the parent cell */
  box-sizing: border-box;   /* Include padding and border in element's total width and height */
  position: relative;       /* Relative positioning for children */
  overflow: hidden;         /* Hide overflow to ensure the progress bar stays within bounds */
}

/* Fill part of the progress bar */
.progress-fill {
  height: 100%;              /* Full height of the container */
  line-height: 20px;         /* Center text vertically */
  text-align: center;        /* Center text horizontally */
  color: white;              /* Text color */
  font-weight: bold;         /* Text weight */
  position: absolute;        /* Absolute positioning to overlap container */
  top: 0;
  left: 0;
  background-color: red;     /* Default color */
  transition: width 0.6s ease; /* Smooth width transition */
}

/* Color classes based on percentage */
.progress-fill.redd {
  background-color: red;
}

.progress-fill.orangee {
  background-color: orange;
}

.progress-fill.greenn {
