.switchR {
  position: relative;
  display: flex;
  align-items: center;
  width: 100px;
  height: 20px;
}

.switchR input { 
  opacity: 0;
  width: 0;
  height: 0;
}

.sliderR {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #fff; /* Default color */
  transition: .4s;
  text-align: center;
  line-height: 20px;
}

.sliderR.roundR {
  border-radius: 20px;
}



input:checked + .sliderR {
  background-color: #e7f311; /* Color when checked */
}

input:checked + .sliderR:before {
  transform: translateX(80px); /* Moves knob */
  background-color: #e7f311; /* Knob color when checked */
}

.switch-labelR {
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  font-size: 12px;
  color: #000;
  width: 100%;
  cursor: pointer;
}


