    margin-left: 15rem;
} */


.save-button:hover {
  color: #ffff;
  background-color: #218838;
}


.lansettingdesktop-icon {
  position: absolute;
  margin-left: 90%;
  margin-top: 4.5%;
  cursor: pointer;
  font-size: 20px;
}