.total-failed {
  display: flex;
  width: 49%;
  height: 200%;
  padding: 10px;
  border-radius: 3px;
  margin-top: -4rem;
  margin-left: 2rem;
  cursor: pointer;
  font-size: 25px;
  z-index: 1;
}

.total-failed h4 {
  position: absolute;
  margin-top: .1rem;
  justify-content: center;
  margin-left: 1rem;
  font-size: 20px;
}

.total-job h4 {
  position: absolute;
  margin-left: 2rem;
  margin-top: -.5rem;
  font-size: 20px;
}
.total-failed .pie-chart {
  margin-left: 5rem;
  box-sizing: border-box;
  display: block;
  height: 500px;
  width: 334px;
}


@media screen and (max-width:1024px){

  .total-job h4 {
    position: absolute;
    margin-left: 0rem;
    margin-top: -.5rem;
    font-size: 20px;
}

.total-cards {

  margin-left: -7rem;

  /* box-shadow: 1px 5px 8px #ccc; */
}

  .total-failed .pie-chart {
    margin-left: -1rem;
    box-sizing: border-box;
    display: block;
    height: 500px;
    width: 334px;
}
}

.loading-overlayy {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5); /* semi-transparent black */
  z-index: 9999; /* Make sure the overlay is above other content */
  display: flex;
  align-items: center;
  justify-content: center;
}

.loading-indicatorr {
color: white;
font-size: 24px;
max-width: 35%;
}

.job-chart-container {
  top: 0;
  margin-top: -3rem;
  max-width: 100%;
  margin-left: 0rem;
  height: 275px;
}

