.total-cards {
    display: flex;
    width: 49%;
    height: 200%;
    border-radius: 3px;
    margin-left: -5rem;
    cursor: pointer;
    font-size: 25px;
    z-index: 1;
  }
  
  .total-cards h4 {
    position: absolute;
    margin-top: -3.6rem;
    justify-content: center;
    margin-left: 10rem;
    font-size: 20px;
  }

  .total-cards h3 {
    position: absolute;
    justify-content: center;
    margin-top: -3.5rem;
    margin-left: 7rem;
    font-size: 20px;
  }

  .total-cards .pie-chart {
    margin-left: 5rem;
    box-sizing: border-box;
    display: block;
    height: 300px;
    width: 300px;
  }

  .loading-overlayy {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.5); 
    z-index: 9999; 
    display: flex;
    align-items: center;
    justify-content: center;
}

.loading-indicatorr {
  color: white;
  font-size: 24px;
  max-width: 35%;
}

.jobs-chart-container {
  top: 0;
  margin-top: -3rem;
  max-width: 100%;
  margin-left: 0rem;
  height: 275px;
}
  




