/* ProgressBarChart.css */

/* Container for all charts */
.chart-container {
  display: grid;
  grid-template-columns: repeat(2, 1fr); /* Fixed 2 columns */
  gap: 16px; /* Space between cards */
  padding: 16px; /* Space around the container */
  margin-left: 6%;
  /* margin-left: 10%; */
}

/* Individual chart card */
.chart-card {
  display: flex;
  flex-direction: column;
  box-sizing: border-box; /* Include padding and border in the element's total width and height */
  padding: 16px;
  border: 1px solid #ddd;
  border-radius: 8px;
  height: auto; /* Ensures that each card has a minimum height */
  width: 555px; /* Ensures responsiveness */
  background-color: #ffffff;
  border-radius: 8px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  overflow: hidden;
  width: 100%;
  max-width: 600px;
  /* overflow-x: scroll; */
}


.modal-content{
  overflow-x: scroll;
  height: 500px;
  width: 100%;
}

/* Title for each chart */
.chart-title {
  font-size: 16px;
  font-weight: bold;
  margin-bottom: 10px;
  padding: 10px 15px;
  background-color: #007bff;
  color: #ffffff;
  font-size: 18px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

/* Custom bar styling */
.custom-bar {
  position: relative;
  height: 100%; /* Ensures bars fill the height of the chart */
}

.progress-bar {
  position: absolute;
  height: 100%;
  background: rgba(0, 255, 0, 0.5); /* Adjust color and transparency */
  animation: progressAnimation 1s ease-in-out forwards;
}

@keyframes progressAnimation {
  from {
    width: 0;
  }
  to {
    width: 100%;
  }
}

/* Optional: Ensure the BarChart fits within the card */
.recharts-wrapper {
  width: 100%;
  height: 100%;
}

/* Label styling on top of the bars */
.recharts-label {
  font-size: 12px;
  font-weight: bold;
  fill: #000;
}

/* Responsive adjustments */
@media (max-width: 768px) {
  .chart-container {
    padding: 10px;
    margin-left: 0; /* Remove left margin for smaller screens */
  }

  .chart-card {
    height: 250px; /* Adjust height for smaller screens */
    width: 100%; /* Ensure it takes full width */
  }

  .chart-title {
    font-size: 14px; /* Adjust font size for smaller screens */
  }
}

@media (max-width: 480px) {
  .chart-container {
    grid-template-columns: 1fr; /* Stack columns on top of each other */
  }

  .chart-card {
    height: 200px; /* Further adjust height for very small screens */
  }

  .chart-title {
    font-size: 12px; /* Adjust font size for very small screens */
  }
}



/* Button to maximize the chart */
.maximize-btn {
  background-color: #ffffff;
  border: 1px solid #ffffff;
  color: #007bff;
  font-size: 14px;
  padding: 5px 10px;
  border-radius: 4px;
  cursor: pointer;
  transition: background-color 0.3s, color 0.3s;
}

.maximize-btn:hover {
  background-color: #007bff;
  color: #ffffff;
}

