/* Ensure the modal overlay and content are styled properly */
.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5); /* semi-transparent black */
  z-index: 9999; /* Make sure the overlay is above other content */
  display: flex;
  align-items: center;
  justify-content: center;
}

.modal-content {
  position: relative; /* Make sure the close button can be positioned absolutely */
  background-color: #fff;
  padding: 20px;
  border-radius: 5px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.3);
  width: 82%;
  max-height: 85%;
  margin-top: 3rem;
  overflow-y: auto;
  margin-left: 4%;
}
