.storage-type-cards {
  display: flex;
  flex-wrap: wrap;
  margin-top: 5px;
  gap: 9px;
  border: 1px solid #ccc;
  background-color: #f8f9fa;
  padding: 20px 14px 0px 14px;
  border-radius: 5px;
}



.storage-type-card {
  border-radius: 5px;
  padding: 10px;
  cursor: pointer;
  display: flex;
  align-items: center;
  margin-bottom: 10px;
  justify-content: center;
  gap: 10px;
  width: 11.3rem;
  background-color: #ffff;
  border: 1px solid #ccc;
}

.storage-type-card:hover {
  box-shadow: 1px 3px 5px #808080;
}


.storage-type-card img {
  max-width: 18%;
  height: auto;
}

.storage-type-card.selected {
  box-shadow: 1px 3px 5px #808080;
  background-color: #eaf248;
}

.sourceB-btn {
  padding: 10px 20px;
  border: 1px solid #000000d3;
  border-radius: 5px;
  background-color: white;
  color: #000000d3;
  cursor: pointer;
  font-size: 16px;
  margin-top: 1%;
  transition: background-color 0.3s, color 0.3s;
}

.sourceB-btn.active,
.sourceB-btn:hover {
  background-color: #000000d3;
  color: white;

}

.popup-backup {
  width: calc(95% - 6rem);
  height: 85%;
  background-color: #fff;
  padding: 20px;
  border-radius: 5px;
  margin-top: 4rem;
  margin-left: 6rem;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.2);
  overflow-y: scroll;
}

.popup-backup .backup-h2 {
  width: 100%;
  text-align: center;
  color: #000;
  font-weight: bolder;
  margin: 0;
  /* margin-top: 2rem; */
}

