  margin: 0
}

.agent-searchB:focus {
  box-shadow: 1px 1px 5px #808080;
}

.agent-SearchIcon {
  position: relative;
  margin: 0;
  margin-left: -3rem;
  font-size: 1.5rem;
}


.popup-backup .agent-searchB-btn {
  width: 35%;
  display: flex;
  align-items: center;
  justify-content: space-between;
  font-size: 20px;
  /* gap: 1rem; */
  margin: 0;
}


.restore-closeB-btn {
  cursor: pointer;
  color: #000; /* Default text color */
  border-radius: 50%; /* Make it a circle */
  height: 40px;
  width: 40px;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 0;
  transition: background-color 0.3s ease, color 0.3s ease; /* Smooth transition for background color and text color */
}

.restore-closeB-btn:hover {
  background-color: #f22; /* Highlight color on hover */
  color: #fff; /* Change text color on hover for better contrast */
}

