/**
 * General variables
 */
 $bdrds: 3px;

 $white: #fff;
 $black: #000;
 $gray: #ccc;
 $salmon: #0462e5;
 $smoky-black: #0e55b9e6;
 
 $ff: 'Montserrat', sans-serif;
 $ff-body: 12px;
 $ff-light: 300;
 $ff-regular: 400;
 $ff-medium: 500;
 
 
 /**
   * General configs
   */
 //  * {
 //    box-sizing: border-box;
 //  }
 
 //  body {
 //    font-family: $ff;
 //    font-size: $ff-body;
 //    line-height: 1em;
