// vikram 11 sep 
.user_options-container {
  position: relative;
  width: 80%;
  top: 12rem;
  /* height: 100%; */
}
 
 .user_options-text {
   display: flex;
   justify-content: space-between;
   width: 100%;
   background-color: $smoky-black;
   border-radius: $bdrds;
 }
 
 /**
   * Registered and Unregistered user box and text
   */
 .user_options-registered,
 .user_options-unregistered {
   width: 50%;
   padding: 75px 45px;
   color: $white;
   font-weight: $ff-light;
 }
 
 .user_registered-title,
 .user_unregistered-title {
   margin-bottom: 15px;
   font-size: 1.66rem;
   justify-content: flex-start;
   line-height: 1em;
   color: #fff;
 }
 
 .user_unregistered-text,
 .user_registered-text {
   font-size: .83rem;
   line-height: 1.4em;
 }
 
 .user_registered-login,
 .user_unregistered-signup {
   margin-top: 30px;
   border: 1px solid $gray;
   border-radius: $bdrds;
   padding: 10px 30px;
   text-transform: uppercase;
   line-height: 1em;
   letter-spacing: .2rem;
   transition: background-color .2s ease-in-out, color .2s ease-in-out;
 }
 
 .user_registered-login:hover,
 .user_unregistered-signup:hover {
   color: $smoky-black;
   background-color: $gray;
 }
 
 /**
   * Login and signup forms
   */
 .user_options-forms {
   position: absolute;
   top: 50%;
   left: 30px;
   width: calc(50% - 30px);
   min-height: 420px;
   background-color: $white;
   border-radius: $bdrds;
   box-shadow: 2px 0 15px rgba($black, .25);
   overflow: hidden;
   transform: translate3d(100%, -50%, 0);
   transition: transform .4s ease-in-out;
 }
 
 .user_forms-login {
   transition: opacity .4s ease-in-out, visibility .4s ease-in-out;
 }
 
 .user_forms-login img {
   margin-left: 42%;
 }
 
 .forms_title {
   margin-bottom: 45px;
   font-size: 1.5rem;
   font-weight: $ff-medium;
   line-height: 1em;
   text-transform: uppercase;
   color: $salmon;
   letter-spacing: .1rem;
 }
 
 .forms_field:not(:last-of-type) {
   margin-bottom: 20px;
 }
 
 .forms_field-input {
   width: 100%;
   border-bottom: 1px solid $gray;
   padding: 6px 20px 6px 6px;
   font-family: $ff;
   font-size: 1rem;
   font-weight: $ff-light;
   color: darken($gray, 30%);
   letter-spacing: .1rem;
   transition: border-color .2s ease-in-out;
 }
 
 .forms_field-input:focus {
   border-color: darken($gray, 30%);
 }
 
 .forms_buttons {
   display: flex;
   justify-content: space-between;
   align-items: center;
   margin-top: 35px;
 
 }
 
 .forms_buttons-forgot {
   font-family: $ff;
   letter-spacing: .1rem;
   color: $gray;
   text-decoration: underline;
   transition: color .2s ease-in-out;
 }
 
 .forms_buttons-forgot:hover {
   color: darken($gray, 10%);
 }
 
 .forms_buttons-action {
   background-color: $salmon;
   border-radius: $bdrds;
   padding: 10px 35px;
   font-size: 1rem;
   font-family: $ff;
   font-weight: $ff-light;
   color: $white;
   text-transform: uppercase;
   letter-spacing: .1rem;
   transition: background-color .2s ease-in-out;
   background-color: #0462e5;
 }
 
 .forms_buttons-action:hover {
   background-color: darken($salmon, 10%);
 }
 
 .user_forms-signup,
 .user_forms-login {
   position: absolute;
   top: 70px;
   left: 40px;
   width: calc(100% - 80px);
   opacity: 0;
   visibility: hidden;
   transition: opacity .4s ease-in-out, visibility .4s ease-in-out, transform .5s ease-in-out;
 }
 
 .user_forms-signup {
   margin-top: -13%;
 }
 
 
 .user_forms-signup {
   transform: translate3d(120px, 0, 0);
 }
 
 .user_forms-signup img {
   margin-left: 42%;
 }
 
 .user_forms-signup .forms_buttons {
   justify-content: flex-end;
 }
 
 .user_forms-login {
   transform: translate3d(0, 0, 0);
   opacity: 1;
   margin-top: -5%;
   visibility: visible;
 }
 
 /**
   * Triggers
   */
 .user_options-forms.bounceLeft {
   animation: bounceLeft 1s forwards;
 
   .user_forms-signup {
     animation: showSignUp 1s forwards;
   }
 
   .user_forms-login {
     opacity: 0;
     visibility: hidden;
     transform: translate3d(-120px, 0, 0);
   }
 }
 
 .user_options-forms.bounceRight {
   animation: bounceRight 1s forwards;
 }
 
 .logininvalid-error {
   color: red;
 }
 
 .password-toggle {
   position: absolute;
   margin-top: .8rem;
   margin-left: -2rem;
 }
 
 @media screen and (max-width:1024px){
  .forms_title{
    margin-left: 7rem;
  }
 }
 
