/* Container for the entire form */
.form-containerf {
    width: 100%;
    max-width: 500px;
    margin: 0 auto;
    padding: 20px;
    border: 1px solid #ddd;
    border-radius: 8px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    background-color: #f9f9f9;
    margin-top: 5%;
  }
  
  /* Form heading */
  .form-headingf {
    text-align: center;
    margin-bottom: 20px;
    font-size: 24px;
    color: #333;
  }
  
  /* Form field container */
  .form-fieldf {
    margin-bottom: 15px;
  }
  
  /* Form field label */
  .form-labelf {
    display: block;
    font-size: 16px;
    margin-bottom: 5px;
    color: #555;
  }
  
  /* Form input fields */
  .form-inputf {
    width: 100%;
    padding: 8px;
    border: 1px solid #ccc;
    border-radius: 4px;
    box-sizing: border-box;
  }
  
  /* Button container */
  .button-containerf {
    display: flex;
    justify-content: space-between;
  }
  
  /* Submit and Proceed buttons */
  .form-buttonf {
    padding: 10px 20px;
    border: none;
    border-radius: 4px;
    font-size: 16px;
    cursor: pointer;
    transition: background-color 0.3s ease;
  }
  
  .button-loginf {
    background-color: #007bff;
    color: white;
  }
  
  .button-loginf:hover {
    background-color: #0056b3;
  }
  
  .button-proceedf {
    background-color: #28a745;
    color: white;
  }
  
  .button-proceedf:hover {
    background-color: #218838;
  }
  