.popup-containergnas {
    position: fixed;
    top: 49%;
    left: 50%;
    /* height: 95%; */
    transform: translate(-50%, -50%);
    background-color: #fff;
    border-radius: 10px;
    padding: 20px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
    z-index: 1000;
    width: 500px; /* Adjust width as needed */
  }
  
  .popup-headernas {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-top: -3%;
  }

  .popup-headernas img {
    max-width: 8%;
  }
  
  .popup-titlenas {
    font-size: 18px;
  }
  
  .close-button {
    border: none;
    background: none;
    cursor: pointer;
    font-size: 20px;
    color: #333;
  }
  
  .popup-contentnas {
    margin-top: -2%;
  }
  
  .form-group {
    margin-bottom: 10px;
  }
  
  .test-connection-button {
    padding: 8px 16px;
    background-color: #007bff;
    color: #fff;
    border: none;
    border-radius: 5px;
    cursor: pointer;
  }
  
  .connection-result {
    margin-top: 10px;
  }
  

  .form-group p{
    cursor: pointer;
    color: #007bff;
  }