.supportForm-component {
    display: grid;
    justify-content: center;
    width: 69%;
    height: auto;
    margin-top: 5rem;
    padding: 20px;
    background-color: bisque;
    border-radius: 5px;
    border: 1px solid #808080;
    box-shadow: 1px 2px 3px #808080;
}

