.supportform-head {
    width: 105%;
    height: 140%;
    top: 0;
    padding: 30px;
    justify-content: center;
    align-items: center;
    text-align: center;
    margin-top: -1.3rem;
    margin-left: -1.3rem;
    /* border-radius: 5px;
    background-image: url('../../../Image/support44.jpg');
    background-repeat: no-repeat;
    background-size: cover; */
}

.supportform-head h3 {
    color: #000;
    font-weight: bolder;
}

.supportform-head input {
    width: 50%;
    border-radius: 30px;
}

.supportform-head i {
    position: absolute;
    font-size: x-large;
    margin-left: 1rem;
    margin-top: 0.8rem;
}

.supportform-content {
    display: flex;
    flex-wrap: wrap;
    /* justify-content: space-around; */
    margin-top: 0rem;
}

.supportform-content-subject {
    text-align: center;
    width: 30%;
    padding: 10px;
    border-radius: 5px;
    cursor: pointer;
    background-color: #ffff;
    border: 1px solid #ccc;
}

.supportform-content-subject:hover {
    background-color: #fff;
    box-shadow: 1px 2px 3px #808080;
}

.supportform-content-subject h4 {
    text-align: center;
    justify-content: center;
    font-weight: lighter;

}




.supportForm-btn button {
    color: #000;
    justify-content: center;
    border: 1px solid #808080;
    border-radius: 5px;
}

.supportForm-btn button:hover {
    color: #ffff;
    background-color: #007bff;
}



.supportform-content-subject {
    position: relative;
    padding: 20px;
    border: 1px solid #ddd;
    border-radius: 8px;
    margin-bottom: 20px;
    transition: background-color 0.3s;
}

.supportform-content-subject:hover {
    background: radial-gradient(black, transparent);
    color: #fff;
    /* background-image: url('../../../Image//support44.jpg'); */
    background-repeat: no-repeat;
    background-size: cover;
    opacity: 0.9;
}

.supportform-content-subject-hover {
    display: none;
}

.supportform-content-subject:hover .supportform-content-subject-hover {
    display: block;
}

.supportform-content-subject-hover button {
    background: transparent;
    border: none;
    color: #ffff;
    font-size: 16px;
    margin-left: 1rem;
    cursor: pointer;
    display: grid;
    align-items: center;
}

.supportform-content-subject-hover i {
    color: #ffff;
    margin-left: 10px;
}

.modal-overlay {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: rgba(0, 0, 0, 0.5);
    display: flex;
    justify-content: center;
    align-items: center;
}

.modal-contentsupport {
    background: #fff;
    padding: 20px;
    border-radius: 8px;
    position: relative;
    height: auto;
    width: 500px;
}

.modal-contentsupport h3 {
    text-align: center;
}

.modal-contentsupport span{
    position: absolute;
    top: 0px;
    color: #000;
    right: 10px;
    background: transparent;
    border: none;
    font-size: 20px;
    cursor: pointer;
}

textarea {
    width: 100%;
    height: 100px;
    margin-bottom: 10px;
    padding: 10px;
    border: 1px solid #ddd;
    border-radius: 4px;
}

.modal-contentsupport button {
    padding: 10px 20px;
    border: none;
    border-radius: 4px;
    background-color: #007bff;
    color: #fff;
    cursor: pointer;
}

.modal-close {
    background:none;
}



.supportform-content-subject {
    position: relative;
    padding: 20px;
    border: 1px solid #ddd;
    border-radius: 8px;
    margin: 10px;
    transition: all 0.3s ease;
}

.supportform-content-subject i,
.supportform-content-subject h4,
.supportform-content-subject p {
    transition: opacity 0.3s ease;
}

.supportform-content-subject-hover {
    display: none;
}

.supportform-content-subject:hover .supportform-content-subject-hover {
    display: block;
}

/* .supportform-content-subject:hover i, */
.supportform-content-subject:hover h4,
.supportform-content-subject:hover p {
    opacity: 0;
}

.supportform-content-subject-hover {
    position: absolute;
    margin-left: 3rem;
    margin-top: -3rem;
}

.hover-button {
    background-color: #007bff;
    color: white;
    border: none;
    padding: 10px 20px;
    border-radius: 5px;
    cursor: pointer;
}

.hover-button i {
    margin-left: 5px;
}



.confirmation-modal {
    background: linear-gradient(45deg, black, transparent);
    padding: 20px;
    color: #ffff;
    border-radius: 8px;
    text-align: center;
}


.confirmation-modal h4 {
    margin: 0;
    font-size: 1.5rem;
    justify-content: center;

}

.confirmation-modal p {
    margin: 10px 0;
}

.confirmation-modal button {
    background: #007bff;
    color: white;
    border: none;
    padding: 10px 20px;
    border-radius: 5px;
    cursor: pointer;
}

.modal-contentsupport.confirmation-modal button:hover {
    background: #0056b3;
}