.destination-container.pinned {
  position: fixed;
  top: 0;
  right: 0;
  width: 300px;
}

.destination-content {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  max-width: 800px;
  height: 77%;
  margin-top: 8rem;
  margin-left: 26rem;
  background-color: #fff;
  z-index: 10;
  border-radius: 5px;
  padding: 20px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  overflow-y: scroll;
}

/* DST.css */
.DST-container {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 80vh;
  width: 78vh;
  padding: 20px;
  margin-left: 10rem;
  background-color: #f0f0f0;
  box-shadow: 1px 3px 5px #808080;
  flex-direction: column;
  /* Added to ensure vertical alignment */
}

.DST-title h1 {
  text-align: center;
  font-weight: bolder;
  font-size: larger;
  margin-top: 0.5rem;
}

.DST-content {
  text-align: center;
  width: 75vh;
  height: 53vh;
  display: flex;
  flex-direction: column;
  /* Ensures each toggle is in a new row */
  align-items: center;
  /* Centers the toggles horizontally */
}

.toggle {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  /* Ensure full width usage */
  padding: 2px;
}

.toggle p {
  margin: 0;
  flex: 1;
  /* Ensure the text takes up remaining space */
  display: flex;
  align-items: center;
}

.switchdst {
  position: relative;
  display: inline-block;
  width: 68px;
  height: 34px;
  margin-right: 10px;
}

.switchdst input {
  opacity: 0;
  width: 0;
  height: 0;
}

.sliderdest {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: -10px;
  right: 0;
  bottom: 0;
  background-color: #ccc;
  transition: 0.4s;
}

.sliderdest:before {
  position: absolute;
  content: "";
  height: 26px;
  width: 26px;
  left: 4px;
  bottom: 4px;
  background-color: white;
  transition: 0.4s;
}

input:checked+.sliderdest {
  background-color: #2196F3;
}

input:checked+.sliderdest:before {
  transform: translateX(43px);
}

/* Rounded sliders */
.sliderdest.round {
  border-radius: 34px;
}

.sliderdest.round:before {
  border-radius: 50%;
}

.dst-save-btn {
  margin-top: 20px;
}

.dst-save-btn button {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  color: #ffff;
  background-color: #2196F3;
  border-radius: 5px;
  border: 1px solid #ccc;
  padding: 5px 10px;
  margin-top: 3rem;
  cursor: pointer;
}

.dst-save-btn button:hover {
  color: #ffff;
  background-color: #1184e3;
}

.toggle i {
  cursor: pointer;
  margin-left: 10px;
  font-size: 1.5rem;
  color: #333;
}

.toggle i:hover {
  color: #2196F3;
}