.DST-title h1 {
    text-align: center;
    font-weight: bolder;
    font-size: larger;
    margin-top: 0.5rem;
}



.toggle {
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100%; /* Ensure full width usage */
    padding: 2px;
}

.toggle p {
    margin: 0;
    flex: 1; /* Ensure the text takes up remaining space */
    display: flex;
    align-items: center;
}

.switchbr {
    position: relative;
    display: inline-block;
    width: 100px;
    height: 34px;
    margin-left: 1rem; /* Add margin to separate from cog icon */
}

.switchdst input {
    opacity: 0;
    width: 0;
    height: 0;
}

.sliderdst {
    position: absolute;
    cursor: pointer;
    top: 0;
    left: 0;
    right: 20px;
    bottom: 0;
    background-color: #ccc;
    transition: 0.4s;
}

.sliderdst:before {
    position: absolute;
    content: "";
    height: 26px;
    width: 26px;
    left: 4px;
    bottom: 4px;
    background-color: white;
    transition: 0.4s;
}

input:checked + .sliderdst {
    background-color: #2196F3;
}

input:checked + .sliderdst:before {
    transform: translateX(43px);
}

/* Rounded sliders */
.sliderdst.round {
    border-radius: 34px;
}

.sliderdst.round:before {
    border-radius: 50%;
}

.dst-save-btn {
    margin-top: 20px;
}

.dst-save-btn button {
    display: inline-flex;
    align-items: center;
    justify-content: center;
    color: #ffff;
    background-color: #2196F3;
    border-radius: 5px;
    border: 1px solid #ccc;
    padding: 5px 10px;
    margin-top: 3rem;
    cursor: pointer;
}

.dst-save-btn button:hover {
    color: #ffff;
    background-color: #1184e3;
}

.toggle i {
    cursor: pointer;
    margin-left: 10px; 
    font-size: 1.5rem;
    color: #333;
}

.toggle i:hover {
    color: #2196F3;
}
