.user-profileform {
  display: flex;
  margin-bottom: 20px;
}

.user-profileform label {
  position: absolute;
  margin-top: -1rem;
}

/* List View */
.user-table {
  width: auto;
  border-collapse: collapse;
  margin-top: 8rem;
  margin-left: 8%;
}

.user-table th,
.user-table td {
  border: 1px solid #ddd;
  padding: 8px;
  width: 300px;
  text-align: left;
}

.user-table th,
.user-table td img {
  max-width: 60%;
}

.user-table th {
  background-color: #f2f2f2;
  text-align: left;
}

/* Card View */
.user-cards {
  display: flex;
  flex-wrap: wrap;
  gap: 20px;
  margin-top: 8rem;
  margin-left: 8%;
}

.user-card {
  border: 1px solid #ddd;
  padding: 20px;
  width: calc(33.333% - 40px);
  /* Adjust width according to your design */
  box-sizing: border-box;
  /* background-color: #ffff;
  border-radius: 2px; */
  box-shadow: 1px 1px 1px #808080;
  background: #fff;
  border-radius: 8px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
}

.user-card:hover {
  background: #fff;
  border-radius: 10px;
  box-shadow: 0 10px 18px rgba(0, 0, 0, 0.1);
}

.user-card img {
  width: 100%;
  height: auto;
  /* margin-top: -1rem; */
}

.user-card img:hover{
  border: 2px solid burlywood;
  border-radius: 50%;
}

.user-card-content h4 {
  margin: 10px 0;
}

.button-container-save button {
  justify-content: center;
  color: #ffff;
  background-color: #277DB0;
  border: 1px solid #ccc;
  border-radius: 2px;

}

.button-container-save button:hover {
  justify-content: center;
  color: #ffff;
  background-color: #277DB0;
  border: 1px solid #ccc;
  border-radius: 2px;

}


.delete-button {
  background: none;
  border: none;
  cursor: pointer;
}


/* user previledge */

.userpreviledge-content {
  width: 100%;
  max-width: 450px;
  /* height: 100%; */
  margin-top: 0rem;
  margin-left: 2rem;
  background-color: ghostwhite;
  /* z-index: 10; */
  border-radius: 5px;
  padding: 20px;
  background: #fff;
  border-radius: 10px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
 
}

.userpreviledge-content:hover {
  box-shadow: 0 10px 18px rgba(0, 0, 0, 0.2);
}

.userpreviledge-content h3 {
  text-align: center;
  margin-bottom: 1rem;
}


.logo-change-contentPW img {
  width: 100%;
  height: auto;
}

.userpreviledge-container {
  display: grid;
  align-items: center;
  grid-template-columns: 1fr 1fr 1fr 1fr 1fr;
  gap: 10px;
}

.userpreviledge-header-content {
  display: flex;
}

.userpreviledge-itemss {
  display: grid;
  padding: 10px;
}

.userpreviledge-item {
  padding: 5px;
  text-align: center;
  align-items: center;
  margin-left: 1rem;
  justify-content: left;
  gap: 5rem;
  display: flex;
}

.userpreviledge-item-header {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 5px;
  font-weight: bold;
  padding: 5px;
  border-radius: 5px;
  /* border: 1px solid #ccc; */
  /* background-color: #e0e0e0; */
}


.userpreviledge-item-header img {
  max-width: 25px;
}

.userpreviledge-item-header h5 {
  font-size: 17px;
  font-weight: bolder;
}

.userpreviledge-item select {
  width: 100%;
  padding: 5px;
  cursor: pointer;
}

.rightsuser-save-btn button {
  color: #000;
  margin-left: 0rem;
  margin-top: 1rem;
  background: none;
  border-radius: 5px;
  text-align: center;
  border: 1px solid #ccc;
  z-index: 10;
}


.rightsuser-save-btn button:hover {
  color: #ffff;
  background-color: #000000b7;
}



.userpreviledge-item.active-background {
  background-color: #007bff;
  /* Change this color to your preferred active color */
  color: #ffff;
  padding: 5px;
  border-radius: 5px;
  box-shadow: 0px 1px 2px #808080;
}


.popup-notification {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.5);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 1000;
}

.popupuserpreviledge-content {
  background: linear-gradient(45deg, black, transparent);
  padding: 20px;
  border-radius: 5px;
  text-align: center;
}

.popupuserpreviledge-content h4 {
  color: #ffff;
  margin: 0;
}

.popupuserpreviledge-content button {
  margin-top: 10px;
