.forgotpass-container {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
    background-color: #f4f4f4;
}

.forgotpass-content {
    background: #fff;
    border-radius: 8px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
    padding: 20px;
    width: 100%;
    max-width: 400px;
}

.forgotpass-content img {
    display: flex;
    margin-left: 10rem;
}

.forgotpass-step {
    display: flex;
    flex-direction: column;
}

.forgotpass-step h2 {
    margin-bottom: 20px;
    font-size: 1.5em;
    color: #333;
}

.forgotpass-step p {
    text-align: center;
    justify-content: center;
    padding: 5px;
    background-color: darkkhaki;
}

.forgotpass-step input {
    margin-bottom: 15px;
    padding: 10px;
    font-size: 1em;
    border: 1px solid #ddd;
    border-radius: 4px;
}

.chnagepassword-submitbtn button {
    padding: 10px;
    font-size: 1em;
    width:97%;
    color: #fff;
    background-color: #007bff;
    border: none;
    border-radius: 4px;
    cursor: pointer;
}

.chnagepassword-submitbtn button:hover {
    background-color: #0056b3;
}


.password-input-group {
    position: relative;
    margin-bottom: 1rem;
}

.password-toggle-btn {
    position: absolute;
    right: 10px;
    top: 40%;
    transform: translateY(-50%);
    border: none;
    background: transparent;
    cursor: pointer;
}

button:disabled {
    cursor: not-allowed;
    opacity: 0.5;
}

