/* Rescheduler.css */
.rescheduler-page {
  padding: 20px;
  background-color: #f9f9f9;
  h1{
    text-align: center;
    color: #007bff;
  }
}

.schedule-container {
  margin-bottom: 20px;
}

.schedule-item {
  margin-bottom: 10px;
  display: flex;
}



.tab-button {
  padding: 10px;
  margin-right: 10px;
  border: 1px solid #ddd;
  border-radius: 4px;
  cursor: pointer;
  background-color: #fff;
}

.tab-button.active {
  background-color: #007bff;
  color: #fff;
}

.allowed-days {
  margin-top: 20px;
  display: flex;
}

.allowed-days-checkboxes {
  margin-bottom: 5px;
  display: flex;
  flex-wrap: wrap;
  gap: 42px;
  text-align: center;
}

