/* Card.css */
.unique-card {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 240px;
  width: 270px;
  background: linear-gradient(#0462e4, #ffffff);
  box-shadow: 0px 6px 6px -3px rgba(255, 255, 255, 0.2),0px 10px 14px 1px rgba(0,0,0,0.14),0px 4px 18px 3px rgba(0,0,0,0.12);
  border-radius: 8px;
  margin-top: 10%;
}

.unique-content {
  display: flex;
  flex-direction: column;
  height: 88%;
  width: 88%;
  background-color: white;
  border-radius: 8px;

}

.unique-top {
  display: flex;
  justify-content: flex-end;
  align-items: center;
  height: 20%;
  background-color: rgba(255, 255, 255, 0.831);
  border-radius: 50%;
}

.unique-top p {
  font-weight: bold;
  font-size: 12px;
}

.unique-color {
  height: 20px;
  width: 20px;
  margin-left: 5px;
  margin-right: 10px;
  border-radius: 50%;
}

.unique-middle {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 40%;
  background: linear-gradient(#007bff, #0056b336 75%);
  padding-left: 15px;
  padding-right: 15px;
}

.unique-middle img {
  width: 60%;
}

.unique-middle div > p:first-child {
  margin-bottom: 5px;
  font-size: 12px;
} 

.unique-middle div > p:last-child {
  margin: 0;
  font-weight: bold;
  font-size: 16px;
}

.unique-bottom {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 50%;
  padding-left: 30px;
  padding-right: 30px;
}

.unique-status {
  background-color: blanchedalmond;
  padding: 5px 10px;
}

.unique-paraRJ {
  display: flex;
  align-items: center;
  color: blue;
}

.unique-paraRJ p {
  margin-right: 10px;
}

.unique-cardLength {
  background-color: rgb(10, 8, 8);
  width: 50px;
  height: 30px;
  color: gold;
  text-align: center;
}

.unique-button {
  background-color: #007bff;
  color: white;
  border: none;
  border-radius: 4px;
  padding: 8px 16px;
  cursor: pointer;
}

.unique-button:hover {
  background-color: #0056b3;
}




.crdbtn{
  padding: 10px 20px;
  border: 1px solid #000000d3;
  border-radius: 5px;
  background-color: white;
  color: #000000d3;
  cursor: pointer;
  font-size: 16px;
  transition: background-color 0.3s, color 0.3s;
}

.crdbtn.active,
.crdbtn:hover {
  background-color: green;
  color: white;
}

.card-containerC {
  display: flex;
  flex-wrap: wrap;
  gap: 1rem; /* Adjust spacing between cards as needed */
  margin-left: 5rem; /* Optional: Adjust if necessary */
  padding: 1rem; /* Optional: Adds padding around the container */
}

.AgentCard {
  flex: 1 1 calc(25% - 1rem); /* Adjusts card width and maintains spacing */
  max-width: calc(25% - 1rem); /* Ensures cards do not exceed their container width */
  box-sizing: border-box; /* Ensures padding and border are included in the card's total width and height */
}


.playbtnsAgntC{
  width: 30%;
}

.PlayPauseBtna{
  font-size: 26px;
  cursor: pointer;
}
.play-buttona.active-buttona {
  color: orange;
}

.pause-buttona.active-buttona {
  color: orange;
}

.cardrtble{
  width: 100%;
  height: 100%;
}
