.topjobs-container {
    padding: 20px;
}

.topjobs-content {
    max-width: 800px;
    margin: -4.5rem auto;
    overflow-y: scroll;
    height: 275px;
}

.topjobs-tabledata {
    width: 100%;
    border-collapse: collapse;
}



.topjobs-tabledata th,
.topjobs-tabledata td {
    border: 1px solid #ddd;
    padding: 8px;
    text-align: left;
}

.topjobs-tabledata th {
    background-color: gold;
}

.topjobs-tabledata tr:nth-child(even) {
    background-color: #f9f9f9;
}

.topjobs-tabledata tr:hover {
    background-color: rgb(252, 227, 89);
}