.info-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.5); /* Dark translucent background */
  display: flex;
  justify-content: center;
  align-items: center;
}

.info-container {
  background: #fff; /* White background for the container */
  padding: 20px;
  border-radius: 8px; /* Rounded corners */
  max-width: 500px;
  width: 90%; /* Adjust width for responsiveness */
  /* text-align: center; */
  position: relative;
  margin-top: 5%;
}

.info-close {
  position: absolute;
  top: 10px;
  right: 15px;
  cursor: pointer;
  font-size: 30px;
  color: #333; /* Color of the close icon */
}

.info-close:hover{
  background-color: #f22;
  border-radius: 40%;
  }

.info-heading {
  font-size: 18px; /* Larger font size for heading */
  margin: 0;
  padding-bottom: 10px; /* Space below the heading */
  font-weight: bold; /* Bold text for emphasis */
}

.info-container p {
  margin: 10px 0; /* Margin for paragraph text */
}

.op {
  position: relative;
  width: 100%; /* Full width for relative positioning */
}
.info-titlee{
  color: #007bff;
  font-weight: bold;
}


