.notificationsms-container {
  padding: 20px;
  /* background-color: #f4f4f4; */
}

.notificationsms-content {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  max-width: 800px;
  height: 77%;
  margin-top: 8rem;
  margin-left: 26rem;
  background-color: #fff;
  z-index: 10;
  border-radius: 5px;
  padding: 20px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  overflow-y: scroll;
}

.logo-change-contentPW img {
  width: 100%;
  height: auto;
}

.notificationSMS-container {
  display: grid;
  align-items: center;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  gap: 10px;
}

.notificationSMS-item {
  /* padding: 10px; */
  text-align: center;
}

.notificationSMS-item-header {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 5px;
  font-weight: bold;
  padding: 5px;
  border-radius: 5px;
  border: 1px solid #ccc;
  /* background-color: #e0e0e0; */
}

.notificationSMS-item-header img {
  max-width: 25px;
}

.notificationSMS-item-header h5 {
  font-size: 17px;
  font-weight: bolder;
}

.notificationSMS-item select {
  width: 100%;
  padding: 5px;
  cursor: pointer;
}

.permission-save-btn button {
  position: absolute;
  color: #000;
  margin-left: 2rem;
  margin-top: 2rem;
  background: none;
  border-radius: 10px;
  text-align: center;
  border: 1px solid #ccc;
  z-index: 10;
}

.permission-save-btn button:hover {
  color: #ffff;
  background-color: #000000b7;
}



.notificationSMS-item.active-background {
  background-color: #007bff; /* Change this color to your preferred active color */
  color: #ffff;
  padding: 5px;
  border-radius: 5px;
  box-shadow: 0px 1px 2px #808080;
}


.popup-notification {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.5);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 1000;
}

.popupnotification-content {
  background: #fff;
  padding: 20px;
  border-radius: 5px;
  text-align: center;
}

.popupnotification-content h4 {
  color: green;
  margin: 0;
}

.popupnotification-content button {
  margin-top: 10px;
  padding: 5px 10px;
  border: none;
  background-color: #007bff;
  color: #fff;
  border-radius: 3px;
  cursor: pointer;
}

.popupnotification button:hover {
  background-color: #0056b3;
}
