.accounts-container {
  padding: 20px;
  /* background-color: #f5f5f5; */
  border-radius: 10px;
  /* box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1); */
}

.accounts-container.pinned {
  position: fixed;
  top: 0;
  right: 0;
  width: 300px;
}

.accounts-container {
  position: relative;
}

.accounts-content {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  max-width: 800px;
  height: 77%;
  margin-top: 8rem;
  margin-left: 26rem;
  background-color: #fff;
  z-index: 10;
  border-radius: 5px;
  padding: 20px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  overflow-y: scroll;
}

.accounts-content h1 {
  text-align: center;
}

.profilesetting-admin-content {
  display: flex;
  /* justify-content: space-around; */
  margin-bottom: 20px;
  align-items: center;
}

.profilesetting-admin-content img {
  justify-content: center;
  border-radius: 75px;
  max-width: 20%;
}

.profilesetting-admin-content span {
  display: flex;
  padding: 10px;
  margin-top: 24px;
  margin-left: 1rem;
  gap: 7px;
  /* background-color: #fff; */
  /* border: 1px solid #ccc; */
  /* box-shadow: 0px 1px 3px #808080; */
  border-radius: 5px;
  flex-wrap: wrap;
}

.profilesetting-admin-content span .admin-maidetail  {
  display: grid;
  text-align: center;
  padding: 5px;
  border-radius: 5px;
  background-color: ghostwhite;
  border: 1px solid #ccc;
  box-shadow: 1px 1px 3px #808080;
}


