/* 21 aug remove z-index vikram */
.menus-page {
  position: absolute;
  padding: 0;
  margin-top: 0rem;
  margin-left: 1rem;
  /* z-index: 2; */
}


.menus-page ul li {
  display: grid;
  width: 202%;
  text-align: center;
}


.menu-button {
  color: #277DB0;
  background-color: #ffff;
  padding: 10px 15px;
  margin: 5px;
  border: 1px solid #277DB0;
  border-radius: 5px;
  cursor: pointer;
  text-decoration: none;
  transition: background-color 0.3s, color 0.3s;
}

.menu-button:hover {
  background-color: #277DB0;
  color: #ffff;
}

.menu-button.active {
  background-color: #277DB0;
  color: #ffff;
}


